var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"cgmp"},[_c('el-row',{attrs:{"type":"flex","justify":"center"}},[_c('el-col',{attrs:{"span":22}},[_c('h1',[_vm._v("An Evolving Sea Surface Temperature Predicting Method Based on Multidimensional Spatiotemporal Influences ")]),_c('h3',[_vm._v("ABSTARCT")]),_c('p',{staticStyle:{"text-align":"justify"}},[_vm._v(" In global climate researches, marine ecosystem researches, and ocean-related applications, it is of considerable significance to accurately observe and predict Sea Surface Temperature (SST). However, various physical and environmental factors affect the changes in SST, making it highly random and uncertain. Therefore, it is still a challenge to propose a highly accurate SST prediction method. SST prediction methods based on the temporal information usually focus on capturing the temporal influence of the historical SST but ignore the spatial influence in the sea area, so these methods meet the performance bottlenecks. In order to fuse the multidimensional spatiotemporal influence and further improve the accuracy of the SST prediction, this paper proposed the Convolutional Gated Recurrent Unit (GRU) with Multilayer Perceptron (CGMP) to predict SST in the Bohai Sea and the South China Sea. The convolutional layer of CGMP can capture the neighbor influence effectively in the spatial dimension, making up for the shortcomings of methods that are based on the temporal information and do not consider the spatial information. The GRU layer and the multilayer perceptron layer of CGMP can process historical information effectively in the temporal dimension. Experiments showed that the prediction performance of CGMP was better than those of other comparison methods in different sea areas, different schemas (daily mean, weekly mean, and monthly mean), and different prediction scales. Besides, the error distribution law in the Bohai Sea daily mean SST prediction was explored. ")]),_c('